@use '../../../../library';

.MuiButton-root {
  &.stripe--button {
    margin: 4px 0;
    background: library.$lightGreen;
    text-decoration: none;
    color: white;
    &:hover {
      background: library.$darkGreen;
    }
  }
}

.MuiBox-root {
  &.user--text {
    margin: 5rem 2rem;
    color: library.$darkGreen
  }

  @media (max-width: 768px) {
    &.user--text {
      margin: 5rem 1rem;
    }
  }
}

.MuiTypography-root {
  &.user--text-details {
    color: library.$darkGreen;
    :a {
      text-decoration: none;
      &:hover {
        color: library.$coral
      }  
    }
  }
}

.MuiPaper-root {
  &.user-profile--card {
    max-width: 90vw;
    box-shadow: 1rem;
    padding: 2rem;
    
    @media (min-width: 768px) {
      width: 350px;
    }
  }
}